import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Axios from "axios";
import SelectSearch from "react-select-search";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;

class FieldTolakanCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kodeBarang",
          text: "Kode barang",
        },
        {
          dataField: "bruto",
          text: "Bruto",
        },
        {
          dataField: "kadarTransaksi",
          text: "%",
        },
        {
          dataField: "netto",
          text: "Netto",
        },
      ],
      listJenis: [],
      listJenisPerhiasan: [],
    };
  }

  componentDidMount() {
    Axios.get(server + "subcategorys2?kodeKelompok=CIOK")
      .then((res) =>
        this.setState({
          listJenis: res.data,
        })
      )
      .catch((err) => console.log(err));
    Axios.get(server + "subcategorys2?kodeKelompok=PERHIASAN")
      .then((res) =>
        this.setState({
          listJenisPerhiasan: res.data,
        })
      )
      .catch((err) => console.log(err));
    this.props.change("noFaktur", this.props.noFaktur);
  }

  onSubmitToko(data) {
    var split = data.split("|");
    this.props.change("kadar", split[1]);
    this.props.change("kadarTransaksi", split[2]);
    this.props.change("kodeJenis", split[0]);
  }
  setNetto() {
    this.props.change("netto", this.props.netto);
  }
  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Jenis</label>
              {this.props.jenisTolakan === "PEMBAYARAN" ? (
                <SelectSearch
                  options={this.state.listJenis.map((obj) => {
                    var payload = {
                      value:
                        obj.kodeJenis +
                        "|" +
                        obj.kadarCetak +
                        "|" +
                        obj.kadarJual,
                      name: obj.namaJenis,
                    };
                    return payload;
                  })}
                  search
                  placeholder="Silahkan Pilih"
                  onChange={(data) => this.onSubmitToko(data)}
                />
              ) : (
                <SelectSearch
                  options={this.state.listJenisPerhiasan.map((obj) => {
                    var payload = {
                      value:
                        obj.kodeJenis +
                        "|" +
                        obj.kadarCetak +
                        "|" +
                        obj.kadarJual,
                      name: obj.namaJenis,
                    };
                    return payload;
                  })}
                  search
                  placeholder="Silahkan Pilih"
                  onChange={(data) => this.onSubmitToko(data)}
                />
              )}
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="kodeJenis"
                label="kodeJenis"
                component={renderField}
                type="text"
                readOnly={true}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="kadar"
                label="Kadar"
                component={renderField}
                type="text"
                readOnly={true}
              />
            </div>
            <div className="col-lg-4 col-md-4 d-none">
              <Field
                name="noFaktur"
                label="noFaktur"
                component={renderField}
                type="text"
                readOnly={true}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="bruto"
                label="Bruto"
                component={renderField}
                type="text"
                onChange={this.setNetto()}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="kadarTransaksi"
                label="Persen"
                component={renderField}
                type="text"
                readOnly={true}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="netto"
                label="Netto"
                component={renderField}
                type="text"
                readOnly={true}
              />
            </div>
            <div className="col-lg-12">
              {this.props.data ? (
                <ToolkitProvider
                  keyField="id"
                  data={this.props.data}
                  columns={this.state.columns}
                  search
                >
                  {(props) => (
                    <div className="form-group">
                      <BootstrapTable
                        {...props.baseProps}
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              ) : null}
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Tambah
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FieldTolakanCustomer = reduxForm({
  form: "FieldTolakanCustomer",
  enableReinitialize: true,
})(FieldTolakanCustomer);
const selector = formValueSelector("FieldTolakanCustomer"); // <-- same as form name

export default connect((state) => {
  const { bruto, kadarTransaksi } = selector(state, "bruto", "kadarTransaksi");
  return {
    netto: (
      parseFloat(bruto ? bruto : 0) *
      (parseFloat(kadarTransaksi ? kadarTransaksi : 0) / 100)
    ).toFixed(3),
  };
})(FieldTolakanCustomer);
