import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import SelectSearch from "react-select-search";
import Axios from "axios";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const server = process.env.REACT_APP_BACKEND_URL;
class FormTolakanPembayaranCustomer extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDateNow = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      dateNow: formatedDateNow,
      columns: [
        {
          dataField: "jenis",
          text: "Jenis",
          footer: "",
        },
        {
          dataField: "kadarCetak",
          text: "Kadar",
          footer: "Total Bruto",
        },
        {
          dataField: "bruto",
          text: "Bruto",
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
        {
          dataField: "kadarTransaksi",
          text: "%",
          footer: "Total Netto",
        },
        {
          dataField: "netto",
          text: "Netto",
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
      ],
      listLokasi: [],
      listSales: [],
      listCustomer: [],
    };
  }

  componentDidMount() {
    Axios.get(server + "storages2?tipeData=LOKASI").then((res) =>
      this.setState({
        listLokasi: res.data,
      })
    );
    Axios.get(server + "storages2?tipeData=SALES").then((res) =>
      this.setState({
        listSales: res.data,
      })
    );
    Axios.get(server + "customers").then((res) =>
      this.setState({
        listCustomer: res.data,
      })
    );
  }
  setFaktur() {
    this.props.change("noFaktur", this.props.noFaktur);
  }
  onSubmitSelect(data, nama) {
    this.props.change(nama, data);
  }
  onSubmitSelectToko(data, nama, nama2) {
    var split = data.split("|");
    this.props.change(nama, split[0]);
    this.props.change(nama2, split[1]);
  }
  render() {
    var namaToko = this.state.listCustomer.map((obj) => {
      var payload = {
        value: obj.namaToko + "|" + obj.namaCustomer,
        name: obj.namaToko,
      };
      return payload;
    });
    var sales = this.state.listSales.map((obj) => {
      var payload = {
        value: obj.kodeLokasi,
        name: obj.namaLokasi,
      };
      return payload;
    });
    var lokasi = this.state.listLokasi.map((obj) => {
      var payload = {
        value: obj.kodeLokasi,
        name: obj.namaLokasi,
      };
      return payload;
    });
    return (
      <>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-4 col-lg-4">
              <Field
                name="noFaktur"
                label="Nomor Faktur"
                type="text"
                component={renderField}
                onBlur={this.setFaktur()}
                readOnly={true}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="tanggal"
                label="Tanggal"
                type="date"
                component={renderField}
                defaultValue={this.state.dateNow}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <label htmlFor="">Lokasi</label>
              <SelectSearch
                options={lokasi}
                search
                placeholder="Silahkan Pilih"
                onChange={(data) => this.onSubmitSelect(data, "kodeLokasi")}
              />
            </div>
            <div className="col-md-4 col-lg-4 d-none">
              <Field
                name="kodeLokasi"
                label="kodeLokasi"
                type="text"
                component={renderField}
                defaultValue={this.state.dateNow}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <label htmlFor="">Sales</label>
              <SelectSearch
                options={sales}
                search
                placeholder="Silahkan Pilih"
                onChange={(data) => this.onSubmitSelect(data, "kodeSales")}
              />
            </div>
            <div className="col-md-4 col-lg-4 d-none">
              <Field
                name="kodeSales"
                label="kodeSales"
                type="text"
                component={renderField}
                defaultValue={this.state.dateNow}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="noBon"
                component={renderField}
                type="text"
                label="No Bon Bayar"
              />
            </div>
            <div className="col-md-4 col-lg-4 d-none">
              <Field
                name="kodeToko"
                label="Nama Toko"
                type="text"
                component={renderField}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <label htmlFor="">Nama Toko</label>
              <SelectSearch
                options={namaToko}
                search
                placeholder="Silahkan Pilih"
                onChange={(data) =>
                  this.onSubmitSelectToko(data, "kodeToko", "namaCustomer")
                }
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="namaCustomer"
                label="Nama Customer"
                type="text"
                component={renderField}
              />
            </div>

            <div className="col-lg-12 col-md-12">
              <div className="row">
                <div className="col-lg-6">
                  <div className="text-left">
                    <button
                      className="btn btn-primary"
                      type="submit"
                      disabled={this.props.onSend}
                    >
                      {this.props.onSend ? (
                        <span
                          className="spinner-border spinner-border-sm mr-3"
                          aria-hidden="true"
                        ></span>
                      ) : null}
                      Simpan
                      <i className="fa fa-paper-plane ml-3"></i>
                    </button>
                  </div>
                </div>
                <div className="col-lg-6">
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      type="button"
                      data-target="#editModal"
                      data-toggle="modal"
                    >
                      Tambah Data
                      <i className="fa fa-plus ml-3"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </form>
      </>
    );
  }
}

FormTolakanPembayaranCustomer = reduxForm({
  form: "FormTolakanPembayaranCustomer",
  enableReinitialize: true,
})(FormTolakanPembayaranCustomer);
export default connect()(FormTolakanPembayaranCustomer);
