import Axios from "axios";
import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import { getUUID } from "../actions/fakturAction";
import { finishSend, getBruto, progressSend } from "../actions/LogicAction";
import { getTabelCustomer } from "../actions/tolakCustomer";
import HeaderCard from "../HeaderCard";
import FormTolakanPembayaranCustomer from "./Component/FormTolakanPembayaranCustomer";
import PrintTolakanCustomer from "./Component/PrintTolakanCustomer";
import FieldTolakanCustomer from "./Element/FieldTolakanCustomer";

const maptostate = (state) => {
  return {
    tabelTolakanCustomer: state.provinsi.tabelTolakanCustomer,
    username: state.provinsi.username,
    tampungBarang: state.provinsi.terimaSupplierTemp,
    noFaktur: state.provinsi.noFaktur,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};
const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class TolakanPembayaranCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jenisTolakan: "PEMBAYARAN",
      columns: [
        {
          dataField: "kodeBarang",
          text: "Jenis",
        },
        {
          dataField: "bruto",
          text: "Bruto",
        },
        {
          dataField: "kadarTransaksi",
          text: "%",
        },
        {
          dataField: "netto",
          text: "Netto",
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
    this.props.dispatch(getTabelCustomer());
    this.props.dispatch(getBruto("tolakanCustomer"));
  }

  handleSubmitBarang(data) {
    var barangDetails = {
      bruto: parseFloat(data.bruto),
      netto: parseFloat(data.netto),
      kodeBarang: data.kodeJenis,
      kadarTransaksi: parseFloat(data.kadarTransaksi),
    };
    console.log(barangDetails);
    var TolakanCustomer =
      JSON.parse(localStorage.getItem("tolakanCustomer")) || [];
    TolakanCustomer.push(barangDetails);
    localStorage.setItem("tolakanCustomer", JSON.stringify(TolakanCustomer));
    this.props.dispatch(getTabelCustomer());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  handlSubmit(data) {
    let isi = {
      noFaktur: data.noFaktur,
      tanggal: data.tanggal,
      noBon: data.noBon,
      kodeLokasi: data.kodeLokasi,
      kodeSales: data.kodeSales,
      namaToko: data.kodeToko,
      kodeCustomer: data.namaCustomer,
      keterangan: "-",
      totalAlatBayar: {
        totalPutusRp: 0,
        totalPutusGr: 0,
        totalTrfRp: 0,
        totalCt: 0,
        totalRsk: 0,
        totalGiroRp: 0,
        totalTunaiRp: 0,
        tipeData: "string",
      },
      alatBayarBarang: JSON.parse(localStorage.getItem("tolakanCustomer")),
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    // INISIALISASI AUTOTABLE
    this.props.dispatch(progressSend());
    const tableRows = [];
    var ciok = JSON.parse(localStorage.getItem("tolakanCustomer")) || [];
    ciok.forEach((ticket, i) => {
      const ticketData = [
        ticket.kadarTransaksi,
        ticket.kodeBarang,
        "",
        ticket.bruto,
        ticket.kadarTransaksi,
        "0.000",
        ticket.netto,
        "0",
        "1",
        "0",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "Kadar",
      "Kode",
      "Ket",
      "Bruto(Gr)",
      "Harga(%)",
      "Diskon",
      "Netto(Gr)",
      "Pkg",
      "Qty",
      "Ongkos(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(server + "cst/dc/tlkbyr", isi)
      .then((res) =>
        Swal.fire({
          position: "top-right",
          text: "Transaksi Berhasil",
          icon: "success",
          timer: 2500,
          showConfirmButton: false,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() =>
            this.props.dispatch(reset("FormTolakanPembayaranCustomer"))
          )
          .then(() =>
            PrintTolakanCustomer(
              "No Trx",
              this.props.noFaktur,
              "Sales",
              data.kodeSales,
              "No Bon",
              data.noBon,
              "Customer",
              data.kodeCustomer,
              "ADMIN",
              data.tanggal,
              "ADMIN",
              columTable,
              "TOLAK PEMBAYARAN CUSTOMER",
              tableRows
            )
          )
          .then(() => this.props.dispatch(getUUID()))
      )
      .catch((err) =>
        Swal.fire({
          position: "top-right",
          text: "Sepertinya ada gangguan, Transaksi Gagal",
          icon: "error",
          timer: 2500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }

  selectJenis = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-d-12">
          <ol className="breadcrumb mb-2">
            <Link to="/KlasifikasiCustomer">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Tolak Pembayaran Customer" />
            <div className="card-body">
              <div className="col-lg-4">
                <div className="form-group">
                  <label htmlFor="">Jenis Tolakan</label>
                  <select
                    name="jenisTolakan"
                    id="jenisTolakan"
                    className="form-control"
                    onChange={this.selectJenis("jenisTolakan")}
                  >
                    <option value="PEMBAYARAN" key="PEMBAYARAN">
                      TOLAKAN PEMBAYARAN
                    </option>
                    <option value="RETURN" key="RETURN">
                      TOLAKAN RETURN
                    </option>
                  </select>
                </div>
              </div>

              <div className="col-lg-12">
                <FormTolakanPembayaranCustomer
                  data={this.props.tabelTolakanCustomer}
                  noFaktur={this.props.noFaktur}
                  onSubmit={(data) => this.handlSubmit(data)}
                  onSend={this.props.onSend}
                />
              </div>
              <div className="col-lg-12 mt-3">
                {this.props.tabelTolakanCustomer ? (
                  <ToolkitProvider
                    keyField="id"
                    data={this.props.tabelTolakanCustomer}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <BootstrapTable
                    keyField="id"
                    data={[]}
                    columns={this.state.columns}
                    noDataIndication="Tidak Ada Data"
                    striped
                    hover
                    condensed
                    bordered={true}
                    headerClasses="header-table"
                  />
                )}
              </div>
            </div>
          </div>
          <div
            id="editModal"
            className="modal fade"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="primary-header-modalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog modal-lg">
              <div className="modal-content">
                <div className="modal-header modal-colored-header bg-primary">
                  <h4 className="modal-title" id="primary-header-modalLabel">
                    Tambah Data Barang
                  </h4>
                  <button
                    id="dismiss_modal1"
                    name="dismiss_modal1"
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-hidden="true"
                  >
                    Ã—
                  </button>
                </div>
                <div className="modal-body">
                  <FieldTolakanCustomer
                    jenisTolakan={this.state.jenisTolakan}
                    data={this.props.tabelTolakanCustomer}
                    noFaktur={this.props.noFaktur}
                    onSubmit={(data) => this.handleSubmitBarang(data)}
                  />
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-light"
                    data-dismiss="modal"
                  >
                    Close
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TolakanPembayaranCustomer);
